import React, { useState, FC } from 'react';
import Modal from '../../components/modal';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import styles from './style.module.scss';
import clsx from 'clsx';
import Radio from '../ui/radio';
import Accordion from '../accordeon';
import Checkbox from '../ui/checkbox';
import { Colors } from '../../types/filter';
import { sizes } from '../../utils/sizes';
import Size from '../ui/size';

type Props = {
  open: boolean;
  setOpen: (o: boolean) => void;
};

export const Filters: FC<Props> = ({ open, setOpen }: Props) => {
  const [rcValue, setRcValue] = useState([0, 30]);
  const [checked, setChecked] = useState(false);
  const onAfterChange = (value: number[] | number) => {
    console.log('value', value);
  };

  return (
    <Modal open={open} onClose={() => setOpen(false)} bottom>
      <h3 className="mb20 f16 "></h3>
      <div className={styles.filter}>
        <h4 className="f16 weight500 mb10"></h4>
        <div className={styles.filter_box}>
          <input className={styles.field} type="text" />
          <span className={styles.dash} />
          <input className={styles.field} type="text" />
        </div>
        <div className={clsx(styles['slider'], 'mb20')}>
          <Slider
            allowCross={false}
            range
            step={1}
            min={1}
            max={168}
            defaultValue={rcValue}
            onChange={onAfterChange}
          />
        </div>
        <div className="mb20">
          <Accordion title="">
            <Radio className="mb10" checked={checked} onChange={() => setChecked(!checked)}>
              <span className="f14 mb10"> </span>
            </Radio>
            <Radio checked={!checked} onChange={() => setChecked(!checked)}>
              <span className="f14"> </span>
            </Radio>
          </Accordion>
        </div>
        <div className="mb20">
          <Accordion title="">
            <div className={styles.colors}>
              {Object.values(Colors).map((c) => (
                <div key={c} className="mb10">
                  <Checkbox name="red" bgColor={c} />
                </div>
              ))}
            </div>
          </Accordion>
        </div>
        <div className="mb20">
          <Accordion title="">
            <div className={styles.colors}>
              {sizes.map((s) => (
                <div key={s} className="mb10">
                  <Size num={s} />
                </div>
              ))}
            </div>
          </Accordion>
        </div>
        <div className="mb20">
          <Accordion title="">
            <Radio className="mb10" checked={checked} onChange={() => setChecked(!checked)}>
              <span className="f14 mb10"></span>
            </Radio>
            <Radio checked={!checked} onChange={() => setChecked(!checked)}>
              <span className="f14"></span>
            </Radio>
          </Accordion>
        </div>
      </div>
    </Modal>
  );
};
